<template>
    <v-ons-page>
      <button :class="['saveBtn', {buttonProgressed: progressed}]" @click="onClick" >Save</button>
      <img id="original-image">
      <div id="physics-world"></div>
      <div id="svg-wrapper"></div>
      <div ref="library" :class="['slide-page', libraryOpened ? 'slide-open': 'slide-close']" @click="libraryOpened=false">
        <ul>
          <lottie-view v-if="contents.length === 0" width="100px" height="100px" :speed="1" :loop=true :autoPlay="true" path="static/progress-dots-blue.json" />
          <li class="contents-item" v-for="(item, index) in contents" :key="index" @click="mapit(index, $event)">
            <svg width="200" height="200" xmlns="http://www.w3.org/2000/svg" viewbox="0 0 100 100">
              <defs v-html="item.contents"></defs>
              <circle cx="100" cy="100" r="100" :fill="'url(#patternx'+index+')'" />
            </svg>
          </li>
        </ul>
      </div>
    </v-ons-page>
</template>

<script>
import Snap from 'snapsvg-cjs'
import Matter from 'matter-js'
import mapper from '../lib/mapper.js'
import LottieView from './LottieView.vue'
import axios from 'axios'

export default {
  name: 'EditPage',
  data () {
    return {
      libraryOpened: false,
      contents: [],
      snap: null,
      lastClicked: null,
      progressed: false
    }
  },
  mounted () {
    let rasterImage = localStorage.getItem('raster')
    document.querySelector('#original-image').src = rasterImage
    let svgContent = localStorage.getItem('vectorized')
    let svgWrapper = document.querySelector('#svg-wrapper')
    svgWrapper.innerHTML = svgContent
    let svg = svgWrapper.querySelector('svg')
    this.snap = Snap(svg)
    let box = this.snap.getBBox()
    this.snap.attr({class: 'vectorsurface', viewBox: `0, 0, ${box.width}, ${box.height}`, preserveAspectRatio: 'xMidYMid meet'})
    /*
    console.log(snap)
    let paths = snap.selectAll('path')
    paths.forEach(e => {
      e.attr({'fill': '#000', 'fill-opacity': '0.3', 'stroke': 'black', 'stroke-width': '0.5', 'stroke-opacity': '0.5'})
      e.touchstart(function (e) {
        this.attr({'fill': 'red', 'fill-opacity': '0.8'})
      })
      e.touchend(function (e) {
        this.attr({'fill': '#000', 'fill-opacity': '0.3'})
      })
    })
    */
    let physicsWrapper = document.querySelector('#physics-world')
    let engine = Matter.Engine.create()
    let render = Matter.Render.create({
      element: document.getElementById('physics-world'),
      engine: engine,
      options: {
        width: physicsWrapper.clientWidth,
        height: physicsWrapper.clientHeight,
        background: 'transparent',
        wireframeBackground: 'rgba(0, 0, 0, 0.2)'
      }
    })

    Matter.Render.run(render)
    let runner = Matter.Runner.create()
    Matter.Runner.run(runner, engine)

    let paths = this.snap.selectAll('path')
    paths.forEach(e => {
      e.click(ev => {
        this.libraryOpened = !this.libraryOpened
        console.log(ev)
        this.lastClicked = ev.target
      })
    })

    mapper.getContentsAsync().then(json => {
      for (let key in json) {
        let contents = json[key].contents.trim()
        if (contents.startsWith('<pattern')) {
          json[key].contents = contents.replace('<pattern', `<pattern id="patternx${key}"`)
        }
      }
      console.log(json)
      this.contents = json
    })
  },
  methods: {
    mapit (idx, event) {
      // event.cancelBubble = true
      let item = this.contents[idx]
      if (item.type === 'svg-pattern') {
        let fragment = Snap.parse(item.contents)
        let defs = this.snap.select('defs')
        defs.add(fragment)
        if (this.lastClicked) {
          this.lastClicked.setAttribute('fill', `url(#patternx${idx})`)
          this.lastClicked.setAttribute('fill-opacity', '1')
        }
      }
    },
    onClick () {
      console.log('save...')
      console.log(this.snap)
      // axios.post('http://10.114.69.121:8090/screen/1234', {data: this.snap.outerSVG()})
      axios.post('https://fierce-lake-44609.herokuapp.com/screen/1234', {data: this.snap.outerSVG()})
    }
  },
  components: {
    LottieView
  }
}
</script>

<style scoped>
#original-image, #physics-world, #svg-wrapper {
  position: fixed;
}

.contents-item:active {
  background-color: #dfd;
}

.saveBtn {
  position: fixed;
  background: #eee;
  border: #333 solid 1px;
  opacity: 0.5;
  width: 100px;
  height: 100px;
  border-radius: 50%;
  z-index: 20;
}
.progressed {
  background: #faa;
}

@media (orientation: landscape) {
  .saveBtn {
    top: 50%;
    right: 30px;
    transform: translate(0, -50%);
  }
  #original-image, #physics-world, #svg-wrapper {
  height:100%;
}
}
@media (orientation: portrait) {
  .saveBtn {
    bottom: 0;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  #original-image, #physics-world, #svg-wrapper {
  width:100%;
}
}

</style>

<style>


@media (orientation: landscape) {
  .vectorsurface {
    height: 100%;
    width: auto;
  }
}
@media (orientation: portrait) {
  .vectorsurface {
    width: 100%;
    height: auto;
  }
}

#svg-wrapper > svg > path {
  fill-opacity: 0.3;
  stroke: white;
  stroke-width: 0.5;
  stroke-opacity: 0.3;
}
#svg-wrapper > svg > path:active {
  fill: red;
}

.slide-page {
  background-color: #fff;
  width: 100%;
  height: 100%;
  top: 0;
  left: 100%;
  position: fixed;
  display: block;
  overflow-y: scroll;
  padding: 10px;
  transition: transform 1s;
}

.slide-open {
  transform: translateX(-100%)
}

.slide-close {
  transform: translateX(0)
}

@keyframes animation-open {
  to { left: 0 }
}
@keyframes animation-close {
  to { left: 100% }
}

.slide-page > ul {
  display: grid;
  grid-template-columns: repeat(auto-fill, 200px);
  list-style-type: none;
  grid-row-gap: 10px;
  grid-column-gap: 10px;
  padding:0;
  margin:0;
}
</style>


