// import Snap from 'snapsvg-cjs'
import axios from 'axios'

function getContentsAsync () {
  return axios.get('https://fierce-lake-44609.herokuapp.com/contents').then(response => {
  // return axios.get('http://10.114.69.121:8090/contents').then(response => {
    return response.data
  })
}

export default {
  getContentsAsync
}



